<!-- 自定义头部1 -->
<template>
	<view class="headone" :style="{'padding-top': topheight +'px'}">
		<image class="img" src="../../../static/img/fanhui.png" @click="fanhui"></image>
		<view class="cont">{{ title }}</view>
		<!-- #ifdef  H5 || APP-PLUS -->
		<image class="img" src="../../../static/img/fenxiang.png" @click="share"></image>
		<!-- #endif -->
	</view>
</template>

<script>
	export default {
		name:"headone",
		data() {
			return {
				topheight:0,
			};
		},
		props:['title','icon'],
		//组件创建之前
		beforeCreate(){
			console.log("1");
		},
		//组件创建完毕执行
		created(){
			console.log("2");
			this.topheight = uni.getSystemInfoSync().statusBarHeight+5;//顶部距离
		},
		//挂载前执行
		beforeMount(){
			console.log("3");
		},
		//组件挂载完毕
		mounted(){
			console.log("4");
		},
		//组件更新前状态
		beforeUpdate(){
			console.log("5");
		},
		//组件更新完毕
		updated(){
			console.log("6");
		},
		//组件销毁前
		beforeDestroy(){
			console.log("7");
		},
		//组件销毁完毕
		destroyed(){
			console.log("8");
		},
		//方法-
		methods:{
			fanhui(){
				this.$emit('fanhui');
			},
			share(){
				this.$emit('share');
			}
		}
	}
</script>

<style lang="scss" scoped>
.headone{
	position: fixed;
	top: 0px;
	left: 0px;
	z-index: 10;
	width: 100%;
	display: flex;
	/* #ifdef  H5 || APP-PLUS */
	justify-content: space-between;
	height: 40px;
	line-height: 40px;
	/* #endif */
	/* #ifdef  MP-WEIXIN */
	height: 35px;
	line-height: 30px;
	/* #endif */
	background: $uni-color-color;
	color: #fff;
}
.cont{
	max-width: 50%;
	margin: 0px 3%;
	overflow: hidden;
	font-size: $uni-title-font;
}
.img{
	width: 20px;
	height: 20px;
	/* #ifdef  H5 || APP-PLUS */
	padding: 10px;
	/* #endif */
	/* #ifdef  MP-WEIXIN */
	padding:5px;
	/* #endif */
}
</style>
